@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';

.web-chat-leave-message {
    $this: &;

    .eureka-tile__content-container {
        @include eureka-flex-row;
        justify-content: center;
        align-items: center;
    }

    &__message-input {
        display: block;
        margin-bottom: 16px;

        .eureka-floating-label-composer__label-container {
            padding-left: 1.2rem;
        }

        .web-chat-message-input {
            width: 85%;
        }

        .web-chat-textarea-autosize::-webkit-scrollbar {
            display: none;
        }
    }

    &__after-control-container {
        @include eureka-flex-column;
        justify-content: flex-end;
        flex: 0 0 auto;
        align-items: flex-end;
        position: absolute;
        top: 7px;
        right: 8px;
    }

    &__counter-container {
        font-size: var(--eureka-core-typography-sub-text-font-size, 1.3rem);
        color: var(--eureka-form-input-placeholder-color-text, #666);
    }

    &__leave-message-content {
        .eureka-callout {
            padding: 0;
            margin-bottom: 8px;

            &__title {
                font-size:
                    var(
                        --eureka-core-typography-headings-level5-font-size
                    );
            }
        }

        #{$this}__email-input {
            width: 100%;
            margin-bottom: 12px;
            padding: 1rem 0;
            border-radius: var(--eureka-form-input-border-radius);

            .eureka-text-input {
                width: 100%;
                padding: 0.8rem 1.2rem;
            }

            .eureka-floating-label-composer__label-container {
                padding: 0.8rem 1.2rem;
            }
        }

        #{$this}__error-msg {
            font-size: var(--eureka-core-typography-footnote-font-size);
            text-align: center;
            margin-top: 0;
            margin-bottom: 8px;
            color: var(--eureka-core-color-error);
        }

        #{$this}__send-msg-btn {
            width: 100%;

            .eureka-button__text {
                font-size: var(--eureka-core-typography-large-font-size);
            }
        }
    }
}
